export default class TestClass {
  constructor() {
    this.test1 = 'Testing this ES6 class - ';
    this.test2 = 'Do I need a transpiler for this?';
  }
  writeTest(num) {
    if (!num) {
      document.write(this.test1);
    } else {
      document.write(this.test2);
    }
  }
}


